<template>
  <el-dialog
    :model-value="visible"
    title="内容对比"
    width="950px"
    class="custom-drawer"
    :before-close="handleClose"
    @open="handlePackageDetails"
  >
    <div class="detail-content">
      <div class="before-modification">
        <p>
          套餐分钟数：{{ packageDetails[0]?.packageMinutes }}分钟（每位坐席）
        </p>
        <p>
          套餐内分钟数单价：{{
            packageDetails[0]?.packageInsideUnitPrice
          }}元/分钟
        </p>
        <p>
          套餐外分钟数单价：{{
            packageDetails[0]?.packageOutsideUnitPrice
          }}元/分钟
        </p>
        <p>坐席费用：{{ packageDetails[0]?.seatFee }}元/位/月</p>
        <p>
          中间号月租：{{ packageDetails[0]?.midNumberFee }}元/号码（{{
            packageDetails[0]?.midNumberRule
          }}坐席对应1码号，不足按1码号计算）
        </p>
        <p>
          小号月租：{{ packageDetails[0]?.smallNumberFee }}元/号码（{{
            packageDetails[0]?.smallNumberRule
          }}坐席对应1码号）
        </p>
        <span>修改前</span>
      </div>
      <div class="after-modification">
        <p>
          套餐分钟数：{{ packageDetails[1]?.packageMinutes }}分钟（每位坐席）
        </p>
        <p>
          套餐内分钟数单价：{{
            packageDetails[1]?.packageInsideUnitPrice
          }}元/分钟
        </p>
        <p>
          套餐外分钟数单价：{{
            packageDetails[1]?.packageOutsideUnitPrice
          }}元/分钟
        </p>
        <p>坐席费用：{{ packageDetails[1]?.seatFee }}元/位/月</p>
        <p>
          中间号月租：{{ packageDetails[1]?.midNumberFee }}元/号码（{{
            packageDetails[1]?.midNumberRule
          }}坐席对应1码号，不足按1码号计算）
        </p>
        <p>
          小号月租：{{ packageDetails[1]?.smallNumberFee }}元/号码（{{
            packageDetails[1]?.smallNumberRule
          }}坐席对应1码号）
        </p>
        <span>修改后</span>
      </div>
    </div>
    <template #footer>
      <el-button type="primary" @click="handleClose">取 消</el-button>
    </template>
  </el-dialog>
</template>
<script lang="ts" setup>
import { packageDetailsApi } from "@api/customer-management-api";
import { ref } from "vue";

const props = defineProps<{
  visible: boolean;
  selectedId: string;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean);
}>();
const packageDetails = ref<any[]>([]);

// 获取套餐详情
function handlePackageDetails() {
  const params = { id: props.selectedId };
  packageDetailsApi.getData(params).then((res) => {
    packageDetails.value = res.data;
  });
}

// 关闭弹框
const handleClose = () => {
  emits("update:visible", false);
};
</script>
<style lang="scss" scoped>
.detail-content {
  display: flex;
  position: relative;
}

.before-modification,
.after-modification {
  flex: 1;
  padding: 20px;
  p {
    line-height: 60px;
  }
  span {
    display: inline-block;
    margin-top: 50px;
  }
}

.detail-content::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  width: 1px;
  background-color: #9ca6b9;
}
</style>
